import Component from "../react/component";
import {diff,diffNode} from "./diif";
const ReactDOM = {
    render
}

function render(vnode, container,dom) {
    return diff(dom,vnode,container)
   // return container.appendChild(_render(vnode)) //_renderè¿”å›èŠ‚ç‚¹å¯¹è±¡
}

export function createComponent(comp, props) {
    let inst
    //å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶ï¼Œå°±åˆ›å»ºå®ä¾‹
    if (comp.prototype && comp.prototype.render) { //ç”±äºç±»ç»„ä»¶éœ€è¦ç»§æ‰¿componentï¼Œå®ç°renderæ–¹æ³•ï¼Œæ‰€ä»¥åœ¨åŸå‹ä¸Šæœ‰render
        inst = new comp(props)
    } else { //å‡½æ•°ç»„ä»¶
        //å¦‚æœæ˜¯å‡½æ•°ç»„ä»¶ï¼Œè½¬æ¢æˆç±»ç»„ä»¶ï¼Œæ–¹ä¾¿ç»Ÿä¸€ç®¡ç†
        inst = new Component(props)
        inst.constructor = comp //å°†ç±»çš„æ„é€ å‡½æ•°æŒ‡å‘è¯¥å‡½æ•°
        //å®šä¹‰denderå‡½æ•°
        inst.render = function () {
            return this.constructor(props)
        }
    }

    return inst //è¿”å›ç±»ç»„ä»¶
}

export function setComponentProps(comp,props) { //compå·²ç»æ˜¯ç±»ç»„ä»¶
        //æ›´æ–°props
    comp.props = props;
      if(!comp.base){
         if(comp.componentWillMount){
             comp.componentWillMount()//æ‰§è¡Œç¬¬ä¸€ä¸ªå£°æ˜å‘¨æœŸ
         } else if(comp.componentWillReceiveProps){
            comp.componentWillReceiveProps()//æ‰€ä»¥ç¬¬ä¸€æ¬¡ä¸ä¼šæ‰§è¡Œè¯¥æ–¹æ³•
         }
      }

    //æ¸²æŸ“ç»„ä»¶
    renderComponent(comp)
}

export function renderComponent(comp) {
    let base
    const renderer=comp.render()
    if(comp.base&&comp.componentWillUpdate){
        comp.componentWillUpdate()
    }
    //base = _render(render)//å¾—åˆ°çœŸå®çš„domå¯¹è±¡
   base= diffNode(comp.base,renderer)
    if(comp.base){
        if(comp.componentDidUpdate){
            comp.componentDidUpdate()
        }
    }else if(comp.componentDidMount){
        comp.componentDidMount()   
    }

    comp.base = base//æŒ‚åœ¨å®ä¾‹ä¸Š

}

function _render(vnode) {
    //å®ç°è™šæ‹Ÿdomçš„æ¸²æŸ“
    //å¦‚æœæ˜¯ç©º
    if (vnode === undefined || vnode === null || typeof vnode === 'boolean') return vnode = ''
    
    if(typeof vnode==='number')   vnode=String(vnode)

    //å¦‚æœæ˜¯å­—ç¬¦ä¸²
    if (typeof vnode === 'string') {
        return document.createTextNode(vnode)
    }
    //å¦‚æœæ˜¯ç»„ä»¶(ç±»ç»„ä»¶å’Œå‡½æ•°ç»„ä»¶)
    if (typeof vnode.tag === 'function') { //å‡½æ•°ç»„ä»¶
        // 1.åˆ›å»ºç»„ä»¶ï¼Œå¯èƒ½æ˜¯å‡½æ•°ç»„ä»¶å’Œç±»ç»„ä»¶ï¼Œéƒ½è½¬æ¢æˆç±»ç»„ä»¶é€šä¸€å¤„ç†
        const comp = createComponent(vnode.tag, vnode.attrs)
        // 2.è®¾ç½®ç»„ä»¶å±æ€§
        setComponentProps(comp)
        // 3.è½¬æ¢æˆèŠ‚ç‚¹å¯¹è±¡è¿”å›
        return comp.base //è¿”å›ç»„è£…å¥½çš„å®ä¾‹å¯¹è±¡
    }

    //å‰©ä¸‹çš„å°±æ˜¯è™šæ‹Ÿdom
    //è§£æ„å‡ºè™šæ‹Ÿdom
    const {
        tag,
        attrs,
        children
    } = vnode

    //åˆ›å»ºæ ¹dom
    const dom = document.createElement(tag)

    //ç»‘å®šå±æ€§
    if (attrs) {
        Object.keys(attrs).forEach(key => {
            const value = attrs[key]
            setAttribute(dom, key, value)
        })
    }

    //æ¸²æŸ“å­èŠ‚ç‚¹ï¼Œé€’å½’
    if(children){
        children.forEach(node => render(node, dom))
    }


    //åŠ è½½åˆ°divä¸Šå¹¶è¿”å›
    return dom
}

export function setAttribute(dom, key, value) {

    //å°†classnameè½¬æˆclass
    if (key === 'className') {
        key = 'class'
    }

    //äº‹ä»¶ç›‘å¬ï¼ŒonClickï¼ŒonBlur
    if (/on\w+/.test(key)) {
        //è½¬å°å†™
        key = key.toLowerCase()
        dom[key] = value || ''
    } else if (key === 'style') { //æ ·å¼å±æ€§
        if (!value || typeof value === 'string') {
            dom.style.cssText = value || ''
        } else if (value && typeof value == 'object') {
            for (let k in value) {
                if (typeof value[k] === 'number') {
                    dom.style[k] = value[k] + 'px'
                } else {
                    dom.style[k] = value[k]
                }
            }
        }
    } else { //å…¶ä»–å±æ€§ï¼Œtitle='123'
        if (key in dom) {
            dom[key] = value || ''
        }
        if (value) {
            dom.setAttribute(key, value)
        } else {
            dom.removeAttribute(key)
        }
    }
}

export default ReactDOM