import { setAttribute,setComponentProps,createComponent } from "./index";
export function  diff(dom,vnode,container) {

const ret=diffNode(dom,vnode)


if(container){
    container.appendChild(ret)
}
    
}

export function  diffNode(dom,vnode) {
    let out=dom
    //å®ç°è™šæ‹Ÿdomçš„æ¸²æŸ“
    //å¦‚æœæ˜¯ç©º
    if (vnode === undefined || vnode === null || typeof vnode === 'boolean') return vnode = ''
    
    if(typeof vnode==='number')   vnode=String(vnode)

    //å¦‚æœæ˜¯å­—ç¬¦ä¸²
    if (typeof vnode === 'string') {
        if(dom){
            if(dom&&dom.nodeType===3){//domå­˜åœ¨ï¼Œå¹¶ä¸”æ˜¯æ–‡æœ¬
                if(dom.textContent!==vnode){
                    //æ›´æ–°æ–‡æœ¬å†…å®¹
                    dom.textContent=vnode
                }

            } 
        }else{
            out=  document.createTextNode(vnode)//åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹ 
        }
        return out
    }
    //å¦‚æœæ˜¯ç»„ä»¶(å‡½æ•°ç»„ä»¶å’Œç±»ç»„ä»¶)
    if(typeof vnode.tag==='function'){
        return diffComponent(out,vnode)
    }

    //éæ–‡æœ¬èŠ‚ç‚¹ï¼Œåˆ†ä¸ºç»„ä»¶å’Œéæ–‡æœ¬domèŠ‚ç‚¹
    if(!dom){
        out=document.createElement(vnode.tag)
    }

    diffAttribute(out,vnode)
     
    //æ¯”è¾ƒå­èŠ‚ç‚¹
    if(vnode.children&&vnode.children.length>0||(out.childNodes&&out.childNodes.length>0)){
        diffChildren(out,vnode.children)
    }
    return out

}

function diffAttribute(dom,vnode){//domåŸæœ‰èŠ‚ç‚¹ï¼Œvnodeæ˜¯è™šæ‹Ÿdom
    const oldAttrs=[] //ä¿å­˜ä¹‹å‰çš„domæ‰€æœ‰å±æ€§
    const newAttrs=vnode.attrs //æ–°çš„domå±æ€§
    const domAttrs= [...document.querySelector('#root').attributes]
    domAttrs.forEach(item=>{
        oldAttrs[item.name]=item.value
    })
    //æ¯”è¾ƒ
    //å¦‚æœåŸæ¥çš„å±æ€§æ²¡åœ¨æ–°çš„å±æ€§é‡Œï¼Œå°±ç§»é™¤
    for(let key in oldAttrs){
        if(!(key in newAttrs)){
            setAttribute(dom,key,undefined) //è®¾ç½®undefinedå°±æ˜¯ç§»é™¤å±æ€§
        }
    }

    //æ›´æ–°å’Œæ–°å¢
    for(let keys in newAttrs){
        if(oldAttrs[keys]!==newAttrs[keys]){
            setAttribute(dom,keys,newAttrs[keys])
        }
    }

}

// å¯¹æ¯”å­èŠ‚ç‚¹
function diffChildren(dom, vchildren) {
    const domChildren = dom.childNodes;
    const children = [];
    const keyed = {};
    // å°†æœ‰keyçš„èŠ‚ç‚¹(ç”¨å¯¹è±¡ä¿å­˜)å’Œæ²¡æœ‰keyçš„èŠ‚ç‚¹(ç”¨æ•°ç»„ä¿å­˜)åˆ†å¼€
    if (domChildren.length > 0) {
        [...domChildren].forEach(item => {
            // è·å–key
            const key = item.key;
            if (key) {
                // å¦‚æœkeyå­˜åœ¨,ä¿å­˜åˆ°å¯¹è±¡ä¸­
                keyed[key] = item;
            } else {
                // å¦‚æœkeyä¸å­˜åœ¨,ä¿å­˜åˆ°æ•°ç»„ä¸­
                children.push(item)
            }

        })
    }
    if (vchildren && vchildren.length > 0) {
        let min = 0;
        let childrenLen = children.length; //2
        [...vchildren].forEach((vchild, i) => {
            // è·å–è™šæ‹ŸDOMä¸­æ‰€æœ‰çš„key
            const key = vchild.key;
            let child;
            if (key) {
                // å¦‚æœæœ‰key,æ‰¾åˆ°å¯¹åº”keyå€¼çš„èŠ‚ç‚¹
                if (keyed[key]) {
                    child = keyed[key];
                    keyed[key] = undefined;
                }
            } else if (childrenLen > min) {
                // alert(1);
                // å¦‚æœæ²¡æœ‰key,åˆ™ä¼˜å…ˆæ‰¾ç±»å‹ç›¸åŒçš„èŠ‚ç‚¹
                for (let j = min; j < childrenLen; j++) {
                    let c = children[j];
                    if (c) {
                        child = c;
                        children[j] = undefined;
                        if (j === childrenLen - 1) childrenLen--;
                        if (j === min) min++;
                        break;
                    }
                }
            }
            // å¯¹æ¯”
            child = diffNode(child, vchild);
            // æ›´æ–°DOM
            const f = domChildren[i];
            if (child && child !== dom && child !== f) {
                // å¦‚æœæ›´æ–°å‰çš„å¯¹åº”ä½ç½®ä¸ºç©ºï¼Œè¯´æ˜æ­¤èŠ‚ç‚¹æ˜¯æ–°å¢çš„
                if (!f) {
                    dom.appendChild(child);
                    // å¦‚æœæ›´æ–°åçš„èŠ‚ç‚¹å’Œæ›´æ–°å‰å¯¹åº”ä½ç½®çš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸€æ ·ï¼Œè¯´æ˜å½“å‰ä½ç½®çš„èŠ‚ç‚¹è¢«ç§»é™¤äº†
                } else if (child === f.nextSibling) {
                    removeNode(f);
                    // å°†æ›´æ–°åçš„èŠ‚ç‚¹ç§»åŠ¨åˆ°æ­£ç¡®çš„ä½ç½®
                } else {
                    // æ³¨æ„insertBeforeçš„ç”¨æ³•ï¼Œç¬¬ä¸€ä¸ªå‚æ•°æ˜¯è¦æ’å…¥çš„èŠ‚ç‚¹ï¼Œç¬¬äºŒä¸ªå‚æ•°æ˜¯å·²å­˜åœ¨çš„èŠ‚ç‚¹
                    dom.insertBefore(child, f);
                }
            }
        })
    }
}


function diffComponent(dom,vnode){
     let comp=dom
    //å¦‚æœç»„ä»¶æ²¡å˜åŒ–ï¼Œé‡æ–°è®¾ç½®props
    if(comp&&comp.constructor===vnode.tag){
        setComponentProps(comp,vnode.attrs)
        //èµ‹å€¼
        dom=comp.base
    }else{//ç»„ä»¶å‘ç”Ÿå˜åŒ–
        if(comp){//ç§»é™¤æ—§ç»„ä»¶
              unmountComponent(comp)
              comp=null
        }

        //1.åˆ›å»ºæ–°ç»„ä»¶
        comp=createComponent(vnode.tag,vnode.attrs)
        //è®¾ç½®ç»„ä»¶å±æ€§
        setComponentProps(comp,vnode.attrs)
        //æŒ‚è½½
        dom=comp.base

    }

    return dom


}
function unmountComponent(comp){
       removeNode(comp.base)

}

function removeNode(dom){
      if(dom&&dom.parentNode){
          dom.parentNode.removeNode(dom)
      }
}
